.app {
	color: white;
	position: relative;
	min-height: 100%;
}

.backgroundContainer {
	position: absolute;
	height: 100%;
	width: 100%;
	z-index: -1;
	overflow: hidden;
	background-color: black;
}

.background {
	background-image: url("/src/static/images/sm.png");
	background-size: cover;
	height: 100%;
	width: 100%;
	transform: scale(1.05);
	filter: blur(20px);
	opacity: 0.5;
}

.banjoJaceWrapper {
	height: 100px;
	width: 100px;
	border: solid #fcfbd4 2px;
	box-shadow: 0px 0px 16px #fcfbd4 inset, 0px 0px 16px #fcfbd4;
}

.banjoJace {
	height: 100%;
	width: 100%;
}

.pageWrapper {
	width: 100%;
	padding: 16px;
	padding-top: 24px;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.contentContainer {
	padding-top: 16px;
	max-width: 800px;
	width: 100%;
	display: flex;
	flex-direction: column;
	align-items: center;
}

.menu {
	margin-top: 16px;
}

@media only screen and (min-width: 600px) {
	.pageWrapper {
		padding-top: 16px;
	}

	.banjoJaceWrapper {
		height: 200px;
		width: 200px;
		box-shadow: 0px 0px 32px #fcfbd4 inset, 0px 0px 32px #fcfbd4;
	}

	.contentContainer {
		padding: 16px 32px;
	}
}
